
import { defineComponent, ref, reactive, computed } from "vue";
import Headbar from "@/components/headbar/index.vue";
import EditBar from "@/components/editBar/index.vue";
import Movement from "@/components/movement/index.vue";
import EditRole from "@/components/editRole/index.vue";

export default defineComponent({
  name: "LayoutMode",
  components: {
    Headbar,
    EditBar,
    Movement,
    EditRole
  },
  setup() {
    let eyeStatus = ref(true);
    let searchValue = ref("");
    let triangleDirect = ref("down");
    let tabOpt = ref("model");
    let angleActiveId = ref(0);
    let isEdit = ref(false);
    let isShowEditBtn = ref(true);
    let isEditRole = ref(false);

    let typeModel = reactive([
      { id: 1, name: "装饰", icon: "tupiantianjia", status: true },
      { id: 2, name: "地编", icon: "kapianzhanshi", status: false },
      { id: 3, name: "家具", icon: "shafa", status: false },
      { id: 4, name: "建筑", icon: "architectrue", status: false },
    ]);
    let decorateList = reactive([
      { id: 1, name: "装饰1" },
      { id: 2, name: "装饰2" },
      { id: 3, name: "装饰3" },
      { id: 4, name: "装饰4" },
      { id: 5, name: "装饰5" },
      { id: 6, name: "装饰6" },
      { id: 7, name: "装饰7" },
      { id: 8, name: "装饰8" },
      { id: 9, name: "装饰9" },
      { id: 10, name: "装饰10" },
      { id: 11, name: "装饰11" },
    ]);

    let roleList = reactive([
      { id: 1, name: "角色1", status: false },
      { id: 2, name: "角色2", status: false },
      { id: 3, name: "角色3", status: false },
      { id: 4, name: "角色4", status: false },
      { id: 5, name: "角色5", status: false },
      { id: 6, name: "角色6", status: false },
      { id: 7, name: "角色7", status: false },
      { id: 8, name: "角色8", status: false },
      { id: 9, name: "角色9", status: false },
      { id: 10, name: "角色10", status: false },
      { id: 11, name: "角色11", status: false },
    ]);

    let effectList = reactive([
      { id: 1, name: "特效1", status: false },
      { id: 2, name: "特效2", status: false },
      { id: 3, name: "特效3", status: false },
      { id: 4, name: "特效4", status: false },
      { id: 5, name: "特效5", status: false },
      { id: 6, name: "特效6", status: false },
      { id: 7, name: "特效7", status: false },
      { id: 8, name: "特效8", status: false },
      { id: 9, name: "特效9", status: false },
      { id: 10, name: "特效10", status: false },
      { id: 11, name: "特效11", status: false },
    ]);

    const clickEye = () => {
      eyeStatus.value = !eyeStatus.value;
    };

    let scrollModelWidth = computed({
      get() {
        return (decorateList.length || 0) * 9;
      },
      set() {},
    });

    let scrollRoleWidth = computed({
      get() {
        return (roleList.length || 0) * 9;
      },
      set() {},
    });

    let scrollEffectWidth = computed({
      get() {
        return (effectList.length || 0) * 9;
      },
      set() {},
    });

    const clickTriangle = () => {
      if (triangleDirect.value == "down") {
        triangleDirect.value = "up";
      } else {
        triangleDirect.value = "down";
      }
      console.log("clickTriangle");
    };

    const clickOpt = (opt: string) => {
      tabOpt.value = opt;
    };

    const clickModelItem = (index: number) => {
      typeModel.map((item, idx) => {
        if (idx == index) {
          item.status = true;
        } else {
          item.status = false;
        }
      });
    };

    const clickAngle = (id: number) => {
      if (angleActiveId.value == id) {
        angleActiveId.value = 0;
      } else {
        angleActiveId.value = id;
      }
    };

    const clickRoleItem = (idx: number) => {
      roleList.map((item, index) => {
        item.status = index == idx;
        return item;
      });
      isEdit.value = true;
      isShowEditBtn.value = true;
    };

    const clickEffectItem = (idx: number) => {
      effectList.map((item, index) => {
        item.status = index == idx;
        return item;
      });
      isEdit.value = true;
      isShowEditBtn.value = false;
    };

    const editModel = () => {
      isEdit.value = true;
      isShowEditBtn.value = false;
    };

    const clickClose = () => {
      isEdit.value = false;
    };

    const clickEdit = () => {
      isEditRole.value = true;
    };

    const closeRoleModel = () => {
      isEditRole.value = false;
    }

    return {
      eyeStatus,
      searchValue,
      typeModel,
      decorateList,
      roleList,
      effectList,
      tabOpt,
      triangleDirect,
      angleActiveId,
      isEdit,
      isShowEditBtn,
      isEditRole,
      scrollModelWidth,
      scrollRoleWidth,
      scrollEffectWidth,
      clickEye,
      clickTriangle,
      clickOpt,
      clickModelItem,
      clickAngle,
      clickRoleItem,
      clickEffectItem,
      editModel,
      clickClose,
      clickEdit,
      closeRoleModel
    };
  },
});
